<template>
  <div>
    <LayoutBox one="平台活动">
      <div class="tool_group">
        <div class="btn_group">
          <router-link to="/adm/marketing/activities/add"><el-button type="success" size="small" icon="el-icon-plus" class="inquire" round>新增活动</el-button></router-link>
        </div>
        <el-form class="input_group" :model="form" :inline="true" size="mini">
          <el-form-item label="时间筛选 ">
            <el-date-picker v-model="form.startTime" type="date" placeholder="开始日期"></el-date-picker>至
            <el-date-picker v-model="form.endTime" type="date" placeholder="结束日期"></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" style="width: 80px" @click="onSearch">查询</el-button>
          </el-form-item>
        </el-form>
      </div>

      <el-table :data="activityList" style="width: 100%">
        <el-table-column type="selection" width="55" align="center" />
        <el-table-column prop="name" label="名称" align="center" show-overflow-tooltip />
        <el-table-column label="活动时间" align="center" width="350" show-overflow-tooltip>
          <template slot-scope="scope">
            <p>{{ _formatDate(scope.row.activityBeginTime) }} 至 {{ _formatDate(scope.row.activityEndTime) }}</p>
          </template>
        </el-table-column>
        <el-table-column label="状态" align="center">
          <template slot-scope="scope">
            <p>{{ scope.row.status === 1 ? '启用' : '禁用' }}</p>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center" width="130">
          <template slot-scope="scope">
            <el-button type="text" @click="onUpdate(scope)">编辑</el-button>
            <el-button v-if="scope.row.status === 1" style="color: #fd9a30" type="text" @click="onEnd(scope)">禁用</el-button>
            <el-button v-else-if="scope.row.status === 0" style="color: #fd9a30" type="text" @click="onStart(scope)">启用</el-button>
            <div>
              <el-popconfirm
                confirm-button-text="好的"
                cancel-button-text="不用了"
                icon="el-icon-info"
                icon-color="red"
                title="这是一段内容确定删除吗？"
                @confirm="apiActivityDelete(scope)"
              >
                <el-button slot="reference" type="text">删除</el-button>
              </el-popconfirm>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="total" @current-change="handleCurrentChange" />
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import { formatDate } from '@/utils/date.js'

export default {
  name: 'AdminActivityList',

  components: {
    LayoutBox
  },

  data() {
    return {
      form: {}, // 查询
      addTime: '', // 添加时间选择
      total: 0, // 秒杀分页总条数
      pageNo: 1, // 现在是第几页
      activityList: [] // 所有的活动的列表包括秒杀团购
    }
  },

  created() {
    this.apiGetActivityList()
  },

  methods: {
    // 分页
    handleCurrentChange(pageNo) {
      this.pageNo = pageNo
      this.apiGetActivityList()
    },
    // 终止
    async onEnd(scope) {
      const res = await this.apiActivityUpOrDown(scope.row.id, 0)
      if (res.data.success) {
        this.$message.success('已终止活动')
        this.apiGetActivityList()
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 启用
    async onStart(scope) {
      const res = await this.apiActivityUpOrDown(scope.row.id, 1)
      if (res.data.success) {
        this.$message.success('已启用活动')
        this.apiGetActivityList()
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 查询
    onSearch () {
      this.apiGetActivityList()
    },
    // 编辑
    onUpdate (scope) {
      this.$router.push({ path: '/adm/marketing/activities/add', query: { id: scope.row.id, type: scope.row.type }})
    },

    // api接口----------------------------------------------------
    // 获取活动列表
    apiGetActivityList() {
      let data = {
        pageNo: this.pageNo
      }
      Object.assign(data, this.form) // 模糊查询
      this.$http.get(this.$api.getActivityList)
        .then((res) => {
          this.activityList = res.data.data
          this.total = res.data.data.total
        })
    },
    // 删除
    apiActivityDelete (scope) {
      this.$http.get(`${this.$api.delectActivity}?activeId=${scope.row.id}`)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('删除成功')
            this.apiGetActivityList()
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 0-上架 1-下架
    async apiActivityUpOrDown(id, state) {
      return await this.$http.get(`${this.$api.statusActivity}?activeId=${id}&status=${state}`)
    },
    // 时间格式化
    _formatDate (date) {
      return formatDate(new Date(date), 'yyyy-MM-dd hh:mm')
    }
  }
}
</script>

<style lang="scss" scoped>
    .tool_group{
      display: flex;
    }
    .input_group{
      margin-left: 30px;
    }
</style>
